<template>
  <div class="box">
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="onClickLeft"
    />
    <em>(,) </em>
    <div class="input-box">
      <van-field
        readonly
        clickable
        label=":"
        :value="certificate"
        @click="showPicker = true"
      />
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
          item-height="100px"
        />
      </van-popup>
      <!--  -->
      <van-field
        v-model="name"
        type="text"
        label=":"
        placeholder=""
        ref="refname"
      />
      <!--  -->
      <van-field
        v-model="identityCard"
        type="text"
        placeholder=""
        label=":"
        ref="refid"
        maxlength="18"
      />
      <van-field
        readonly
        clickable
        label=":"
        :value="type"
        @click="showType = true"
        class="type"
      />
      <van-popup v-model="showType" round position="bottom">
        <van-picker
          show-toolbar
          :columns="passengerType"
          @cancel="showType = false"
          @confirm="onType"
          item-height="100px"
        />
      </van-popup>
      <em></em>
      <van-field
        v-model="phone"
        type="digit"
        placeholder=""
        label=":"
        ref="refphone"
      />
    </div>
    <!--button -->
    <article class="btn">
      <button type="button" @click="enterBtn"></button>
    </article>
    <article class="footer">
      <em><i>i</i></em>
      <strong>
        /
      </strong>
    </article>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Newly",
  data() {
    return {
      name: "",
      type: "",
      phone: "",
      identityCard: "",
      show: false,
      isShow: false,
      certificate: "",
      showPicker: false,
      showType: false,
      columns: [
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      passengerType: ["", "", "", ""],
    };
  },
  components: {},
  methods: {
    //
    enterBtn() {
      console.log(
        this.$store.state.passengerListAll[
          this.$store.state.passengerListAll.length - 1
        ].id + 1
      );
      //18
      let regular18 =
        /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
      //15
      let regular15 =
        /^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$/;
      //
      let regularPhone =
        /^1((34[0-8])|(8\d{2})|(([35][0-35-9]|4[579]|66|7[35678]|9[1389])\d{1}))\d{7}$/;
      let id =
        this.$store.state.passengerListAll[
          this.$store.state.passengerListAll.length - 1
        ].id + 1;
      // eslint-disable-next-line no-unused-vars
      let temp = {
        id: id,
        certificate: this.certificate,
        name: this.name,
        identityCard: this.identityCard,
        type: this.type,
      };
      if (this.name === "") {
        this.$toast("");
        this.$refs.refname.focus();
      } else if (this.identityCard === "") {
        this.$toast("");
        this.$refs.refid.focus();
      } else if (
        regular18.test(this.identityCard) ||
        regular15.test(this.identityCard)
      ) {
        if (this.phone === "") {
          this.$toast("");
          this.$refs.refphone.focus();
        } else if (!regularPhone.test(this.phone)) {
          this.$toast("");
          this.phone = "";
          this.$refs.refphone.focus();
        } else if (
          //some
          this.$store.state.passengerListAll.some(
            (item) => item.identityCard == this.identityCard
          )
        ) {
          this.$toast.fail("");
        } else {
          this.$toast.success("");
          this.$store.state.passengerListAll.push(temp);
          this.$router.push({
            name: "rider",
            query: {
              flag: true,
            },
          });
        }
      } else {
        this.identityCard = "";
        this.$toast("");
        this.$refs.refid.focus();
      }
    },
    onConfirm(value) {
      this.value = value;
      this.showPicker = false;
    },
    onType(value) {
      this.type = value;
      this.showType = false;
    },
    onClickLeft() {
      this.$router.back();
    },
    //input
  },
  mounted() {},
  watch: {
    // eslint-disable-next-line no-unused-vars
    identityCard(newvalue, oldvalue) {
      console.log(oldvalue); // 
      if (!/^[a-zA-Z0-9]*$/.test(newvalue)) {
        this.identityCard = newvalue = newvalue.replace(/[^a-zA-Z0-9]/g, ""); // 
      }
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  font-size: 28px;
  /deep/.van-nav-bar__content {
    background-color: #4b8fed;
    i,
    .van-nav-bar__text,
    .van-nav-bar__title {
      color: #fff;
    }
  }
  em {
    display: inline-block;
    height: 88px;
    width: 100%;
    font-size: 28px;
    box-sizing: border-box;
    padding: 20px 32px;
    background-color: #f6f6f6;
    line-height: 50px;
  }
  .btn {
    width: 100%;
    height: auto;
    box-sizing: border-box;
    button {
      width: 84%;
      margin: 46px 60px;
      height: 88px;
      background-color: #4b8fed;
      color: #fff;
      border: none;
    }
  }
  .footer {
    em {
      padding-left: 0px;
    }
    i {
      display: inline-block;
      width: 35px;
      height: 35px;
      border-radius: 20px;
      line-height: 35px;
      text-align: center;
      background-color: black;
      color: #fff;
      vertical-align: middle;
    }
    width: 100%;
    box-sizing: border-box;
    padding: 40px 30px;
    strong {
      color: #7e7e7e;
    }
  }
  /deep/.van-cell:first-child,
  .type {
    &:before {
      position: absolute;
      content: "";
      width: 20px;
      height: 20px;
      transform: rotate(-45deg);
      border-bottom: 5px solid #7e7e7e;
      border-left: 5px solid #7e7e7e;
      top: 24px;
      right: 40px;
    }
  }
}
</style>
