import Mock from "mockjs";
// 
Mock.setup({
  timeout: "200-600",
});
// 
const data = Mock.mock({
  data: [
    {
      username: "admin",
      password: "000000",
    },
    {
      username: "wre",
      password: "666666",
    },
    {
      username: "hopu",
      password: "888888",
    },
  ],
});
console.log(data);
//  get
var getQuery = (url, name) => {
  // -1
  const index = url.indexOf("?");
  if (index !== -1) {
    const queryStr = url.slice(index + 1).split("&"); //
    for (var i = 0; i < queryStr.length; i++) {
      const itemArr = queryStr[i].split("="); //["username","admin","pwd","000"]
      if (itemArr[0] == name) {
        return itemArr[1];
      }
    }
  }
  return null;
};

Mock.mock(/\/api\/get\/login/, "get", (options) => {
  const username = getQuery(options.url, "username");
  const pwd = getQuery(options.url, "pwd");
  var userlist = {};
  //  map filter foreach evey some
  var resDate = data.data.map((item) => {
    // console.log(item);
    //
    if (username === item.username && pwd === item.password) {
      userlist = item;
      return true;
    } else {
      return false;
    }
  });
  console.log(resDate); //true // false
  // es7 includes
  if (resDate.includes(true)) {
    return {
      code: 200,
      message: "",
      data: userlist,
    };
  } else {
    return {
      code: -1,
      message: "",
    };
  }
});
