import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    show: false,
    month: new Date().getMonth() + 1, //
    toDay: new Date().getDate(), //
    startstation: "", //
    endstation: "", //
    isStart: false, //
    isEnd: false, //
    result: [], //
    animation: false, //
    promise: [], //
    ticketList: [], //
    ticketPice: false, //
    pice: {}, //
    //
    passengerListAll: [
      {
        id: 0,
        certificate: "",
        name: "",
        identityCard: 412422200306015823,
        type: "",
      },
      {
        id: 1,
        certificate: "",
        name: "",
        identityCard: 412422200245015823,
        type: "",
      },
    ], //
    //
    passengerList: [],
    //
    order: [],
    maxdate: new Date(
      new Date().getFullYear(),
      new Date().getMonth(),
      new Date().getDate() + 16
    ).getTime(),
    newtime: new Date(
      new Date().getFullYear(),
      new Date().getMonth(),
      new Date().getDate()
    ).getTime(),
    isYesterday: true,
    isTomorrow: false,
    filteritem: "",
    name: "",
    //
    history: [],
    hotStation: ["", "", "", "", "", ""],
  },
  getters: {
    week(state) {
      //
      let date = new Date(
        `${new Date().getFullYear()}/${state.month}/${state.toDay}`
      );
      return "".charAt(date.getDay());
    }, //
    //
    isYesterday(state) {
      if (
        new Date(
          new Date().getFullYear(),
          state.month - 1,
          state.toDay
        ).getTime() === state.newtime
      ) {
        return true;
      }
    },
    isTomorrow(state) {
      if (
        state.maxdate -
          new Date(
            new Date().getFullYear(),
            state.month - 1,
            state.toDay
          ).getTime() ===
        86400000
      ) {
        return true;
      }
    },
  },
  mutations: {
    //
    addHistory(state, value) {
      state.history.unshift(value);
      let temp = (list) => [...new Set(list)];
      state.history = temp(state.history);
    },
    // 
    stationChange(state) {
      state.animation = true;
      // 
      setTimeout(() => {
        state.animation = false;
      }, 1000);
      let temp = state.endstation;
      state.endstation = state.startstation;
      state.startstation = temp;
    },
    // axiospromise
    getDate(state, date) {
      //
      date = date.sort((a, b) => {
        return (
          a.start_time.replace(/\D/g, "") - b.start_time.replace(/\D/g, "")
        );
      });
      state.ticketList = state.promise = date;
    },
    //
    filtrationPromise(state) {
      state.show = false;
      state.filteritem = "";
      //
      if (state.result.length > 0) {
        for (let i = 0; i < state.result.length; i++) {
          state.filteritem += state.result[i];
        }
        state.ticketList = state.promise.filter((value) => {
          return state.filteritem.indexOf(value.train_type) !== -1;
        });
      } else {
        return (state.ticketList = state.promise);
      }
    },
    //
    shortestTime(state) {
      state.ticketList = state.ticketList.sort((a, b) => {
        return (
          a.start_time.replace(/\D/g, "") - b.start_time.replace(/\D/g, "")
        );
      });
    },
    // 
    addtoDy(state) {
      let adddate = new Date(
        new Date().getFullYear(),
        state.month - 1,
        state.toDay + 1
      );
      if (adddate.getTime() < state.maxdate) {
        state.month = adddate.getMonth() + 1;
        state.toDay = adddate.getDate();
      }
    },
    deltoDy(state) {
      let adddate = new Date(
        new Date().getFullYear(),
        state.month - 1,
        state.toDay - 1
      );
      if (adddate.getTime() >= state.newtime) {
        state.month = adddate.getMonth() + 1;
        state.toDay = adddate.getDate();
      }
    },
    //
    next: (state) => {
      state.toDay++;
      if (
        state.toDay >
        new Date(
          new Date().getFullYear(),
          new Date().getMonth() + 1,
          0
        ).getDate()
      ) {
        state.month += 1;
        state.toDay = 1;
      }
    },
  },
  actions: {
    //axios
    getdate(context) {
      const settime = `${context.state.month}${context.state.toDay}`;
      const endstation = context.state.endstation;
      const startstation = context.state.startstation;
      axios
        .get("/api/get/ticket/", {
          params: {
            start: startstation,
            end: endstation,
            date: settime,
          },
        })
        .then((res) => {
          context.commit("getDate", res.data.data);
        })
        .catch((err) => {
          context.commit("getDate", err);
        });
    },
    // +1
    next: ({ commit }) => {
      commit("next");
    },
  },
  modules: {},
});
